.horizontal-bar-chart-container {
  padding: 5%;
}

#horizontal-bar-chart {
  width: 50vw;
  height: 80vw;
}

.bar {
  fill: #e63520;
}

.bar:hover {
  fill: orange;
}

.axis path,
.axis line {
  fill: none;
  stroke: #D4D8DA;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}
.x path {
  display: none;
}
.toolTip {
  position: absolute;
  display: none;
  min-width: 80px;
  height: auto;
  background: none repeat scroll 0 0 #ffffff;
  border: 1px solid #6F257F;
  padding: 14px;
  text-align: center;
}

.y-axis {
  font-size: 14px;
}
