.heading {
  &__hero {
    display: inline-block;
    font-family: 'Crimson Text', serif;
    font-weight: normal;
    font-size: 56px;
    padding: 1em 25%;
    text-align: center;
    text-shadow: rgba(0, 0, 0, 0.5) 0px 0px 2px, rgba(0, 0, 0, 0.3) 0px 0px 20px;
    &_char {
      background: rgba(255,255,255,0.3);
    }
  }

  &__subtitle {
    color: #fff;
    font-size: 25px;
    padding: 2em 0;
    text-align: center;
  }

  &__caption {
    font-size: 18px;
  }

  &__secondary {
    font-size: 55px;
    padding: 0 0 1em;
  }

  &--shadow {
    background: rgba(0,0,0,0.3);
    display: inline-block;
    padding: 15px 20px;
  }
}
