/* ANIMATION */
@import url("https://fonts.googleapis.com/css?family=Crimson+Text&display=swap");
@import url("https://fonts.googleapis.com/css?family=Lato&display=swap");
@import url("http://fonts.googleapis.com/css?family=Open+Sans:400,300,700");
@keyframes pulse-bg {
  0% {
    transform: scale(1); }
  70% {
    transform: scale(1.1); }
  100% {
    transform: scale(1); } }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-20px); }
  60% {
    transform: translateY(-15px); } }

html {
  background-color: #000;
  font-family: Helvetica, Arial, sans-serif; }

html, body {
  background-color: #081118;
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  line-height: 1.5;
  margin: 0 auto;
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
  width: 100%; }

h1, h2, h3, h4, h5, h6, p {
  margin: 0; }

label {
  display: inline-block; }

img {
  height: auto;
  width: 100%; }

.center {
  margin: 0 auto;
  width: 400px; }

.the-families {
  padding: 5% 15%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .the-families__image {
    height: 220px;
    overflow: hidden; }
    .the-families__image-wrapper {
      width: 35%; }
  .the-families__caption {
    margin: 0 0 0 10%;
    width: 50%; }

.heading__hero {
  display: inline-block;
  font-family: 'Crimson Text', serif;
  font-weight: normal;
  font-size: 56px;
  padding: 1em 25%;
  text-align: center;
  text-shadow: rgba(0, 0, 0, 0.5) 0px 0px 2px, rgba(0, 0, 0, 0.3) 0px 0px 20px; }
  .heading__hero_char {
    background: rgba(255, 255, 255, 0.3); }

.heading__subtitle {
  color: #fff;
  font-size: 25px;
  padding: 2em 0;
  text-align: center; }

.heading__caption {
  font-size: 18px; }

.heading__secondary {
  font-size: 55px;
  padding: 0 0 1em; }

.heading--shadow {
  background: rgba(0, 0, 0, 0.3);
  display: inline-block;
  padding: 15px 20px; }

.intro {
  display: inline-block;
  position: relative;
  overflow: auto;
  padding: 0 5%; }

article {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  color: #ffffff;
  background-color: #22283f;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  article:before {
    content: "mobile";
    display: none; }
    @media only screen and (min-width: 1050px) {
      article:before {
        content: "desktop"; } }
  @media only screen and (min-width: 1050px) {
    article[data-hijacking="on"] {
      overflow: hidden; } }

.parent {
  position: relative; }

.narrative-item {
  transition: color 200ms; }

.sticky-outer {
  position: absolute;
  height: calc(100% + 200vh);
  width: 40%;
  top: -80vh;
  z-index: 1; }

.floating-box {
  height: 100vh;
  background: transparent;
  padding: 100px;
  box-sizing: border-box;
  text-align: center;
  font-size: 30px; }

[data-animation="opacity"] .cd-section > div {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover; }

[data-animation="opacity"] .cd-section:nth-of-type(2) > div {
  background-image: url(/assets/images/moses-2.jpg); }

[data-animation="opacity"] .cd-section:nth-of-type(4) > div {
  background-image: url(/assets/images/moses-4.jpg); }

[data-animation="opacity"] .cd-section:nth-of-type(5) > div {
  background-image: url(/assets/images/moses-5.jpg); }

@media only screen and (min-width: 1050px) {
  .cd-section h2 {
    font-size: 4rem;
    font-weight: 300; }
  [data-hijacking="on"] .cd-section {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%; }
  [data-hijacking="on"] .cd-section > div {
    visibility: visible; }
  [data-hijacking="off"] .cd-section > div {
    opacity: 0; }
  [data-animation="opacity"] .cd-section.visible > div {
    z-index: 1; } }

@media only screen and (min-width: 1050px) {
  .cd-section:first-of-type > div::before {
    display: none; } }

@media only screen and (min-width: 1050px) {
  .cd-section > div {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    /* Force Hardware Acceleration */
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden; } }

.singlePage {
  position: relative;
  color: #fff; }
  .singlePage__inner {
    height: 100vh; }
  .singlePage__1 {
    min-height: 100vh;
    height: 100%; }
  .singlePage__2 {
    min-height: 100vh;
    height: 100%; }
  .singlePage__3 {
    min-height: 100vh;
    height: 100%; }

.map_wrapper {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: white; }

#map {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: absolute; }

.button-box {
  padding-left: 10%;
  padding-right: 10%; }

#go-button {
  display: block;
  position: relative;
  float: right;
  width: 30%;
  height: 40px;
  padding: 10px;
  border: none;
  border-radius: 3px;
  font-size: 12px;
  text-align: center;
  color: #fff;
  background: #20d1e6; }

#back-button {
  display: block;
  position: relative;
  float: left;
  width: 30%;
  height: 40px;
  border: none;
  border-radius: 3px;
  font-size: 12px;
  text-align: center;
  color: #fff;
  background: #20d1e6; }

.sticky-outer-map {
  padding-top: 5%;
  padding-left: 10%;
  padding-right: 10%; }

#floating-box-map {
  position: relative;
  float: center;
  padding: 20px;
  text-align: center;
  font-size: 15px;
  line-height: 140%;
  color: black;
  background-color: #ffffff;
  opacity: 0.9;
  filter: alpha(opacity=90); }

.scroll-cta {
  cursor: pointer;
  border-top: 2px solid orange;
  padding: 10px 0;
  margin: 5% auto;
  position: relative;
  text-align: center;
  width: 5%; }

.scroll-cta__link {
  color: #fff;
  font-size: 12px;
  text-decoration: none;
  text-transform: uppercase; }

.scroll-cta__link:after {
  animation: bounce 2s infinite ease-in-out;
  background: url(data:image/svg+xml;base64,) no-repeat center center/100%;
  color: #fff;
  content: "";
  display: block;
  margin: 0 auto;
  padding: 1em 0;
  height: 35px;
  width: 35px; }

.horizontal-bar-chart-container {
  padding: 5%; }

#horizontal-bar-chart {
  width: 50vw;
  height: 80vw; }

.bar {
  fill: #e63520; }

.bar:hover {
  fill: orange; }

.axis path,
.axis line {
  fill: none;
  stroke: #D4D8DA;
  stroke-width: 1px;
  shape-rendering: crispEdges; }

.x path {
  display: none; }

.toolTip {
  position: absolute;
  display: none;
  min-width: 80px;
  height: auto;
  background: none repeat scroll 0 0 #ffffff;
  border: 1px solid #6F257F;
  padding: 14px;
  text-align: center; }

.y-axis {
  font-size: 14px; }

.cd-section {
  height: 100vh;
  position: relative; }

.cd-section h2 {
  line-height: 100vh;
  text-align: center;
  font-size: 2.4rem; }

.cd-section:first-of-type > div {
  background-image: url("/assets/images/1-moses-mug-pic.jpg"); }

.cd-section:nth-of-type(2) > div {
  background-color: #2e5367; }

.cd-section:nth-of-type(3) > div {
  background-image: url("/assets/images/moses-chickens.jpg"); }

.cd-section:nth-of-type(4) > div {
  background-color: #fcb052; }

.cd-section:nth-of-type(5) > div {
  background-color: #f06a59; }

.landing {
  background: url(/assets/images/tersius-van-rhyn-unsplash.jpg) center/cover;
  color: #fff;
  padding: 0 10%;
  height: 100%;
  margin: 0 auto;
  min-height: 100vh; }

.video {
  height: 100vh;
  margin: 0 auto;
  position: absolute;
  width: 100%; }

.video__intro {
  object-fit: fill;
  width: 100%;
  height: 100vh;
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0; }

.video__content {
  padding: 5% 15%;
  z-index: 9999;
  position: absolute; }

#video-background {
  right: 0;
  bottom: 0;
  min-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover; }
